import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MaterialModule } from '@jgpl/shared';
import { RouterModule } from '@angular/router';
import { WebfooterModule } from '@jgpl/shared/webfooter/webfooter.module';
import { WebheaderModule } from '@jgpl/shared/webheader/webheader.module';
import { PoliciesComponent } from './policies.component';


@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    MaterialModule,
    WebheaderModule,
    WebfooterModule,
    RouterModule.forChild([
      { path: '', component: PoliciesComponent  }
    ])
  ]
})
export class PoliciesModule { }
